module.exports = "import React, {Component} from 'react';\nimport {\n  IdealBankElement,\n  injectStripe,\n  StripeProvider,\n  Elements,\n} from 'react-stripe-elements';\n\nclass _IdealBankForm extends Component {\n  handleSubmit = (ev) => {\n    ev.preventDefault();\n    if (this.props.stripe) {\n      this.props.stripe\n        .createSource({\n          type: 'ideal',\n          amount: 1099,\n          currency: 'eur',\n          //You can specify a custom statement descriptor\n          statement_descriptor: 'ORDER AT11990',\n          owner: {\n            name: ev.target.name.value,\n          },\n          redirect: {\n            return_url: 'https://your-website.com/ideal-redirect',\n          },\n        })\n        .then((payload) => console.log('[source]', payload));\n    } else {\n      console.log(\"Stripe.js hasn't loaded yet.\");\n    }\n  };\n  render() {\n    return (\n      <form onSubmit={this.handleSubmit}>\n        <label>\n          Name\n          <input name=\"name\" type=\"text\" placeholder=\"Jane Doe\" required />\n        </label>\n        <label>\n          iDEAL Bank\n          <IdealBankElement\n            className=\"IdealBankElement\"\n            style={{\n              base: {\n                fontSize: '16px',\n                color: '#424770',\n                letterSpacing: '0.025em',\n                padding: '10px 14px',\n                '::placeholder': {\n                  color: '#aab7c4',\n                },\n              },\n              invalid: {\n                color: '#9e2146',\n              },\n            }}\n          />\n        </label>\n        <button>Pay</button>\n      </form>\n    );\n  }\n}\n\nconst IdealForm = injectStripe(_IdealBankForm);\n\nexport class IdealDemo extends Component {\n  render() {\n    return (\n      <StripeProvider apiKey=\"pk_test_6pRNASCoBOKtIshFeQd4XMUh\">\n        <Elements>\n          <IdealForm />\n        </Elements>\n      </StripeProvider>\n    );\n  }\n}\n"


